import { useCallback, useRef, useEffect } from "react";
import CleanArabicString from "../auxiliary/CleanArabicString";
import search from "../../style/css/search.module.css";

const SearchBox = ({ storedData, setData }) => {
  const filterString = useRef(false);

  const handleOnSearchChange = useCallback(() => {
    if (
      typeof filterString.current.value === "string" &&
      filterString.current.value.length > 0
    ) {
      const ss = CleanArabicString(filterString.current.value);
      const ss1 = ss.split(" ");
      console.log(
        "Search String:" + filterString.current.value + " is being set to:" + ss
      );
      setData(() => {
        return storedData.filter((element) => {
          const search_Item =
            " " + element.number + " " + CleanArabicString(element.name) + " ";
          let matches = 0;
          ss1.forEach((word) => {
            const i = search_Item.search(word);
            i > 0 ? ++matches : (matches = 0);
          });
          return matches > ss1.length - 1 ? true : false;
        });
      });
    } else setData(() => storedData);
  }, [storedData, setData]);

  const newSearch = () => {
    if (
      typeof filterString.current.value === "string" &&
      filterString.current.value.length > 0
    ) {
      filterString.current.value = "";
      handleOnSearchChange();
    }
    filterString.current.focus();
  }
  
  useEffect(() => {
    handleOnSearchChange();
  }, [handleOnSearchChange]);

  return (
    <div className={search.box}>
      <button htmlFor="boxinput" className={search.boxbtn} onClick={newSearch}>
         
      </button>
      <input
        name="boxinput"
        className={search.input}
        type="search"
        id="filterString"
        onChange={handleOnSearchChange}
        lang="ar"
        ref={filterString}
      />
    </div>
  );
};
export default SearchBox;
