import { useCallback, useEffect } from "react";
import { useState } from "react";
import add from "../../style/css/form.module.css";

const AddForm = ({ activeElement, setActiveElement, addItem, updateItem }) => {
  const [number, setNumber] = useState(() => {
    return activeElement ? activeElement.number : "";
  });
  const [name, setName] = useState(() => {
    return activeElement ? activeElement.name : "";
  });
  const [email, setEmail] = useState(() => {
    return activeElement ? activeElement.email : "";
  });
  const [phone, setPhone] = useState(() => {
    return activeElement ? activeElement.phone : "";
  });
  const [college, setCollege] = useState(() => {
    return activeElement ? activeElement.college : "";
  });

  const initForm = useCallback(() => {
    if (activeElement && activeElement.number) {
      setNumber(() => activeElement.number);
      setName(() => activeElement.name);
      setEmail(() => activeElement.email);
      setPhone(() => activeElement.phone);
      setCollege(() => activeElement.college);
    }else{
      setNumber(() => '');
      setName(() => '');
      setEmail(() => '');
      setPhone(() => '');
      setCollege(() => '');
    }
  }, [activeElement]);

  useEffect(() => {
    initForm();
  }, [initForm,activeElement]);

  const saveStudent = async (e) => {
    e.preventDefault(); // prevent reload
    console.log(number, name, email, phone, college);
    activeElement && activeElement.number
      ? updateItem({
          id: activeElement.id,
          number: number,
          name: name,
          email: email,
          phone: phone,
          college: college,
        })
      : addItem({
          number: number,
          name: name,
          email: email,
          phone: phone,
          college: college,
        });
    setActiveElement(null
    // {
    //   id: '',
    //   number: '',
    //   name: '',
    //   email: '',
    //   phone: '',
    //   college: '',
    // }
    );
  };

  return (
    <>
      <div className={add.formcontainer}>
        <h1 className={add.title}>   </h1>
        <form className={add.formcards} name="form" onSubmit={saveStudent}>
          <div className={add.formcard}>
            <label className={add.label} htmlFor="number">
                <span className={add.star}>*</span>
            </label>
            <div className={add.forminp}>
              <input
                style={{
                  color: number && "black",
                }}
                type="text"
                name="number"
                placeholder="9878391666"
                className={add.forminputs}
                value={number}
                onChange={(e) => setNumber(e.target.value)}
              />
            </div>
            <div className={add.formerrorbar} id="number_error"></div>
          </div>

          <div className={add.formcard}>
            <label className={add.label} htmlFor="name">
               <span className={add.star}>*</span>
            </label>
            <div className={add.forminp}>
              <input
                style={{
                  color: name && "black",
                }}
                type="text"
                placeholder=" "
                name="name"
                className={add.forminputs}
                id="name"
                value={name}
                onChange={(e) => setName(e.target.value)}
                lang="ar"
              />
            </div>
            <div className={add.formerrorbar} id="name_error"></div>
          </div>
          <div className={add.formcard}>
            <label className={add.label} htmlFor="email">
                <span className={add.star}>*</span>
            </label>
            <div className={add.forminp}>
              <input
                style={{
                  color: email && "black",
                }}
                type="email"
                name="email"
                placeholder="abc@gmail.com"
                className={add.forminputs}
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                lang="en"
              />
            </div>
            <div className={add.formerrorbar} id="email_error"></div>
          </div>
          <div className={add.formcard}>
            <label className={add.label} htmlFor="phone">
               <span className={add.star}>*</span>
            </label>
            <div className={add.forminp}>
              <input
                type="phone"
                name="phone"
                style={{
                  color: phone && "black",
                }}
                placeholder="0234456678"
                className={add.forminputs}
                value={phone}
                onChange={(e) => setPhone(e.target.value)}
              />
            </div>
            <div className={add.formerrorbar} id="phone_error"></div>
          </div>
          <div className={add.formcard}>
            <label className={add.label} htmlFor="college">
               <span className={add.star}>*</span>
            </label>
            <div className={add.forminp}>
              <input
                style={{
                  color: college && "black",
                }}
                type="text"
                name="college"
                placeholder="  "
                className={add.forminputs}
                value={college}
                onChange={(e) => setCollege(e.target.value)}
              />
            </div>
            <div className={add.formerrorbar} id="college_error"></div>
          </div>
          <input
            type="submit"
            value={activeElement ? " " : " "}
            id="submit"
            className={add.formsubmit}
          />
        </form>
      </div>
    </>
  );
};
export default AddForm;
