import header from '../../style/css/header.module.css'
import { exportExcel } from '../auxiliary/excel'
import ConfLogo from '../images/confLogo.png'
import WpuLogo from '../images/wpuLogo.svg'
import DisplayCounters from './displayCounters'
import SearchBox from './SearchBox'

const Header = ({storedData, setData})=>{

    const exportAttendence =()=>{
        const tempData = storedData.filter((element)=>{
          return (element.date1 || element.date2 || element.date3 || element.date4 || element.date5 || element.date6 )
        })
        exportExcel(tempData);
      }
return(
<>
<div className={header.countersWrapper}>
    <DisplayCounters storedData={storedData} />
</div>
<div className={header.searchWrapper}>
    <SearchBox storedData={storedData} setData={setData} />
</div>
<div className={header.conflogoWrapper}>
  <img className={header.conflogo} height={100} src={ConfLogo} alt="" />
</div>
<div className={header.orglogoWrapper}>
  <img className={header.wpulogo} src={WpuLogo} alt="" onClick={exportAttendence} />
</div>
</>)
}
export default Header