import axios from 'axios'
import { useCallback, useState, useEffect } from 'react'
import { DisplayCard } from "./components/main/displayCard.jsx"
import AddForm from "./components/main/addForm"
import app from "./style/css/app.module.css"
import baseURL from './components/config/parameters.js'
import Header from './components/main/header.jsx'

function App() {
  const [state,setState]= useState(()=>{return {data:[],element:{},message:''}})
  const [data,setData]= useState(()=>[])
  const [activeElement, setActiveElement]=useState(()=>{})

  const getItems = useCallback(async () => {
    const res = await axios.get(`${baseURL}all`);
    setState(()=>res.data)
    setData(res.data.data)
  },[]);


  const addItem = useCallback(async (element) => {
    const res = await axios.post(`${baseURL}`,element);
    setState(()=>res.data)
    setData(res.data.data)
  },[]);
  
  const updateItem = useCallback(async (element) => {
    const id = element.id
    const res = await axios.patch(`${baseURL}${id}`,element);
    setState(()=>res.data)
    // setData(res.data.data)
    getItems()
  },[getItems]);

  const deleteItem = useCallback(async (element) => {
    const id = element.id
    const res = await axios.delete(`${baseURL}${id}`);
    setState(()=>res.data)
    // setData(res.data.data)
    getItems()
  },[getItems]);

  useEffect(()=>{
    getItems();
  },[getItems])

  return (
      <div className={app.app}>
      <header className={app.header} >
        <Header storedData={state.data} setData={setData} ></Header>
      </header>
      <main className={app.contents}>
      <div className={app.cardscontainer} > 
        {state.message === "3" ? (
          <div className={app.card} key={state.element.id}>
            <h1>: {state.element.id}</h1>
            <h1>: {state.element.name}</h1>
            <h1>: {state.element.number}</h1>
          </div>
        ) : ((data && (data instanceof Array)) &&
          data.map((element) => {
            return (
              <div key={element.id} title={element.name} className={app.card}>
                 <DisplayCard element={element} deleteItem={deleteItem} updateItem={updateItem} setActiveElement={setActiveElement} ></DisplayCard>
              </div>
              );
          })
        )}
        </div>
        <div className={app.formcontainer}>
          <AddForm activeElement={activeElement} setActiveElement={setActiveElement}
           addItem={addItem} updateItem={updateItem}></AddForm>
        </div>
        </main>
           <footer className={app.footer}></footer>
        </div>
    );
}
export default App;
