import React, { useEffect, useState, useCallback } from "react";
import counter from "../../style/css/counters.module.css";

const DisplayCounters = ({ storedData }) => {
  const [counters, setCounters] = useState(() => {
    return {
      date1: storedData.reduce((partialSum, a) => partialSum + a.date1, 0),
      date2: storedData.reduce((partialSum, a) => partialSum + a.date2, 0),
      date3: storedData.reduce((partialSum, a) => partialSum + a.date3, 0),
      date4: storedData.reduce((partialSum, a) => partialSum + a.date4, 0),
      date5: storedData.reduce((partialSum, a) => partialSum + a.date5, 0),
      date6: storedData.reduce((partialSum, a) => partialSum + a.date6, 0),
    };
  });

  const getTotal = useCallback(() => {
    if (storedData && storedData.length > 0) {
      counters.date1 = storedData.reduce(
        (partialSum, a) => partialSum + a.date1,
        0
      );
      counters.date2 = storedData.reduce(
        (partialSum, a) => partialSum + a.date2,
        0
      );
      counters.date3 = storedData.reduce(
        (partialSum, a) => partialSum + a.date3,
        0
      );
      counters.date4 = storedData.reduce(
        (partialSum, a) => partialSum + a.date4,
        0
      );
      counters.date5 = storedData.reduce(
        (partialSum, a) => partialSum + a.date5,
        0
      );
      counters.date6 = storedData.reduce(
        (partialSum, a) => partialSum + a.date6,
        0
      );
      setCounters(() => counters);
    }
  }, [storedData, counters]);

  useEffect(() => {
    if(storedData)
    getTotal();
  }, [getTotal,storedData]);

  return (
    <div className={counter.container}>
      <div className={counter.f1}>
        <div className={counter.c}>
          <span className={counter.label}> </span>
          <div  className={counter.value} >{counters.date1}</div>
        </div>
        <div className={counter.c}>
          <span className={counter.label}> </span>
          <div  className={counter.value}>{counters.date2}</div>
        </div>
        <div className={counter.c}>
          <span className={counter.label}> </span>
          <div className={counter.value} >{counters.date3}</div>
        </div>
      </div>
      <div className={counter.f2}>
        <div className={counter.c}>
          <span className={counter.label}> </span>
          <div className={counter.value} >{counters.date4}</div>
        </div>
        <div className={counter.c}>
          <span className={counter.label}></span>
          <span className={counter.value}>{counters.date5}</span>
        </div>
        <div className={counter.c}>
          <span className={counter.label}> </span>
          <span className={counter.value}>{counters.date6}</span>
        </div>
      </div>
    </div>
  );
};
export default DisplayCounters;
