// import { useNavigate } from "react-router-dom";
import College from "../auxiliary/GetCollege";
import card from '../../style/css/card.module.css'

export const DisplayCard = ({element, deleteItem, updateItem, setActiveElement}) => {
  // const nav = useNavigate()
  return (
    <>
    <div className={card.cardcontents}>
        <h3>{element.number}</h3>
        <h2 className={card.h2}>{element.name}</h2>
        <College college={element.college} />
         {element.email &&<h3>  : <span className={card.email}>{element.email}</span></h3>}
         {element.email &&<h3>  : {element.phone}</h3>}
      </div>
      <div className={card.attendenceWrapper}>
        <h3></h3>
        <div className={card.f}>
          <div className={card.f1}>
            <button
              className={card.f1btn}
              style={{
                backgroundColor: element.date1 && 'green'
              }}
              onClick={() =>
                {element.date1 = !element.date1
                 updateItem(element)}}
            >
              
            </button>
            <button
              className={card.f1btn}
              style={{
                backgroundColor: element.date2 && 'green'
              }}
              onClick={() =>{
                element.date2 = !element.date2
                updateItem(element)}} >
              
            </button>
            <button
              className={card.f1btn}
              style={{
                backgroundColor: element.date3 && 'green'
              }}
              onClick={() =>{
                element.date3 = !element.date3
                updateItem(element)}} >
              
            </button>
          </div>
          <div className={card.f2}>
            <button
              className={card.f2btn}
              style={{
                backgroundColor: element.date4 && 'green'
              }}
              onClick={() =>{
                element.date4 = !element.date4
                updateItem(element)}} >
              
            </button>
            <button
              className={card.f2btn}
              style={{
                backgroundColor: element.date5 && 'green'
              }}
              onClick={() =>{
                element.date5 = !element.date5
                updateItem(element)}} >
              
            </button>
            <button
              className={card.f2btn}
              style={{
                backgroundColor: element.date6 && 'green'
              }}
              onClick={() =>{
                element.date6 = !element.date6
                updateItem(element)}} >
              
            </button>
          </div>
        </div>
      </div>
      <div className={card.controlwrapper}>

        <button className={card.cwbtn} onClick={() =>
          {setActiveElement(()=>element)}}></button>
        <button className={card.cwbtn} onClick={() =>{deleteItem(element)}}>
          
        </button>
      </div>
    </>
  );
}
