import React from "react";

function GetCollege({college}) {
  let co = (college)? college: 0;
  let c ="";
  switch (co) {
    case 1: {
      c = " ";
      break;
    }
    case 2: {
      c = "";
      break;
    }
    case 3: {
      c = "  ";
      break;
    }
    case 4: {
      c = "";
      break;
    }
    case 5: {
      c = " ";
      break;
    }
    case 6: {
      c = "   ";
      break;
    }
    default:
      c = " ";
  }
  return <h3> : {c}</h3>;
}
export default GetCollege;
